   <el-row class="notePC">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					学习笔记
   				</span>
   	  </el-col>
   <!-- 	  <el-col :span="2" :offset="20" class="more">
   	  		更多	  
   	  </el-col> -->
     </el-col>
	 <el-col class="searchCommon">
	 		 <span class="vimg spanCo">
	 			  <img src="../../assets/img/time.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">
	 			时间排序
	 		 </span>
	 		 <span class="vimg spanCo">
	 		 	  <img src="../../assets/img/px.png" >
	 		 </span>
	 		 <span class="imgTitle spanCo">笔记名称</span>
	 		 <el-col :span="4" class="imput">
	 		 <el-input placeholder="输入相关笔记名称" v-model="input">
	 		  </el-input>
	 		 </el-col>
	 		 <el-button type="primary" ><img src="../../assets/img/search.png" >搜索</el-button>
	 </el-col>
	 <el-col :span="24" class="bottom">
	<!-- 	 <div class="btop">
		 	<div class="bcommon" v-for="(item,index) in btns" :key="index" @click="select(item,index)" :class="{active:index==nowIndex}">
		 		{{item.title}}
		 	</div>
		 </div> -->
		 <div class="bbottom">
		 	<div class="comBox" v-for="(item,index) in records" :key="index">
		 		<div class="comTop">
		 			<!-- <div class="leftBtn">
		 				<div class="circleBig" @click="showC">
		 					<div class="circle" :class="{activeC:circleBig}">
		 					</div>
		 				</div>
		 			</div> -->
					<div class="leftBtnText">
						{{item.className}}
					</div>
					<div class="rightText" @click="dele(item)">
						<img src="../../assets/img/dele.png" ><span>删除</span>
					</div>
		 		</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		发布人:<span class="bigText">{{item.name}}</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">{{item.id}}</span>
					</div>
				<!-- 	<div class="">
						推演时间:<span class="bigText">2021-09-23-12 09:03:22</span>
					</div> -->
					<div class="">
						分类:<span class="bigText">理论</span>
					</div>
				 </div>
				 <div class="startBtn" @click="gonote(item)">
				 	进入
				 </div>
		 	</div>
			<div class="comBox" v-show="false">
				<div class="comTop">
				<!-- 	<div class="leftBtn">
						<div class="circleBig" @click="showC">
							<div class="circle" :class="{activeC:circleBig}">
							</div>
						</div>
					</div> -->
					<div class="leftBtnText">
						职业与创业胜任力胜任力…
					</div>
					<div class="rightText">
						<img src="../../assets/img/dele.png" ><span>删除</span>
					</div>
				</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		主讲人:<span class="bigText">张朝阳</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">01209131121</span>
					</div>
					<div class="">
						推演时间:<span class="bigText">2021-09-23-12 09:03:22</span>
					</div>
					<div class="">
						分类:<span class="bigText">理论</span>
					</div>
				 </div>
				 <div class="startBtn">
				 	进入
				 </div>
			</div>
			<div class="comBox" v-show="false">
				<div class="comTop">
				<!-- 	<div class="leftBtn">
						<div class="circleBig" @click="showC">
							<div class="circle" :class="{activeC:circleBig}">
							</div>
						</div>
					</div> -->
					<div class="leftBtnText">
						职业与创业胜任力胜任力…
					</div>
					<div class="rightText">
						<img src="../../assets/img/dele.png" ><span>删除</span>
					</div>
				</div>
				 <div class="devider"></div>
				 <div class="comInfo">
				 	<div class="">
				 		主讲人:<span class="bigText">张朝阳</span>
				 	</div>
					<div class="">
						编号:<span class="bigText">01209131121</span>
					</div>
					<div class="">
						推演时间:<span class="bigText">2021-09-23-12 09:03:22</span>
					</div>
					<div class="">
						分类:<span class="bigText">理论</span>
					</div>
				 </div>
				 <div class="startBtn">
				 	进入
				 </div>
			</div>
		 </div>
	<!-- 	 <el-col :span="24">
			 <div class="circleBig fillS2" @click="showC">
			 	<div class="circle" :class="{activeC:circleBig}">
			 	</div>
			 </div>
			 全选
			 <div class="downBtn">
			 	批量删除
			 </div>
		 </el-col> -->
	 </el-col>
	 <el-col :span="24" class="cpage">
	 			 <el-pagination
	 			   background
					@current-change="current"
	 				   :prev-text='prevT'
	 				   :next-text='nextT'
	 			   layout="prev, pager, next"
	 			   :total="total">
	 			 </el-pagination>
	 </el-col>
   </el-row>
</template>

<script>
import btns from '../../assets/config/note.json' 
// import {myNote,removeById}  from '../../api/apis/user.js'
import {noteLists,deleteNote} from '@/api/test/user.js'
export default {
	name:'notePC',
	data(){
		return {
			prevT:'上一页',
			nextT:'下一页',
			status:'进行中',
			status1:'',
			status2:'已完成',
			oldIndex:0,
			nowIndex:0,
			circleBig:false,
			input:'',
			btns:[],
			records:[],
			total:0
		}
	},
	mounted() {
		this.btns=btns.btns
		let that=this
		
		// myNote().then(res=>{
		// 	that.records=res.data.records
		// 	that.total=res.data.total
		// })
		noteLists({current:1,size:10}).then(res=>{
			that.records=res.data.data
			that.total=res.data.total
		})
	},
	methods:{
		select(item,index){
			this.nowIndex=index
		},
		showC(){
			this.circleBig=true
		},
		gonote(item){
			localStorage.setItem('noteItem',JSON.stringify(item))
			this.$router.push({path:"/student/sec/noteP"})
		},
		dele(item){
			// removeById(item).then(res=>{
			// 	 this.$message({
			// 	          message: '恭喜你，删除成功！',
			// 	          type: 'success'
			// 	        });
			// })
			deleteNote({id:item.id}).then(res=>{
				 this.$message({
				          message: '恭喜你，删除成功,请刷新！',
				          type: 'success'
				        });
			})
		},
		current(val){
			let that=this
			noteLists({current:val,size:10}).then(res=>{
			that.records=res.data.data
			that.total=res.data.total
		})
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/notePC.css';
</style>



